import { useState, useCallback } from 'react';

import { ProcessEnum } from '../enum';
import { Nullable } from '../types';

import { ProcessType } from './types';

export const useHttp = () => {
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<Nullable<string>>(null);
  const [process, setProcess] = useState<ProcessType>(ProcessEnum.WAITING); // ,       

  const request = useCallback(
    async (
      url: string,
      method = 'GET',
      body = null,
      headers = { 'Content-Type': 'application/json' },
    ) => {
      setLoading(true); //       true
      setProcess(ProcessEnum.LOADING); //     

      try {
        const response = await fetch(url, { method, body, headers });

        if (!response.ok) {
          throw new Error(`Can not fetch ${url}, status : ${response.status}`);
        }

        const data = await response.json(); //      json

        setLoading(false);

        return data; //   ,   ,  ""  (  MarvelService)  API
      } catch (err: any) {
        setLoading(false);
        setError(err.message);
        setProcess(ProcessEnum.ERROR); //  
        throw err;
      }
    },
    [],
  );

  const clearError = useCallback(() => {
    setError(null);
    setProcess(ProcessEnum.LOADING);
  }, []); //  ,    -  id   ,    

  return { loading, request, error, clearError, process, setProcess };
};
